.bconstructor {    
    display: grid;
    grid-template-rows: auto 1fr;    
    margin-top: 40px;
    align-items: end;    
    justify-content: center;
    flex: 1;
    row-gap: 8px;
    grid-auto-rows: 1fr;
    grid-auto-columns: 1fr;
}



.bconstructor>.list>* {
    flex: 0 auto;
}

.bconstructor  span {
    margin-right: 0 ;
}

.list {   
    margin-right: 0;
    width: 100%;
    flex: 1 1 auto;
    margin: 0;
    padding: 0;
    max-height: 54vh;
    overflow-y: scroll;
    overflow-x: hidden;
}
 
.listItem {
    flex-grow: 0;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    padding: 0.5% 0 0.5% 0;
    justify-content: center;
}

.listItem span {
    margin-left:-1.5% ;
    margin-right: 2%;
}

.listItem:last-child {
    margin-bottom: 0.5%;
}

.listItem:first-child {
    margin-top: 0.5%;
}


.currency {        
    align-items: end;
    justify-content: right; 
}

.orderButton {    
    display: flex;
    align-items: center;
    justify-content: right;
}

.splitter {
    display: grid;
    grid-template-columns: 32px 1fr;
}